@import url('https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c:wght@400;700&display=swap');

*, *::before, *::after {
  margin: 0;
  padding: 0;

  box-sizing: border-box;
}

body {
  color: #fff;
  font-family: 'M PLUS Rounded 1c', sans-serif;
  font-size: 16px;
  line-height: 1.5;

  background-color: #84C5D7;
}

.wrapper {
  max-width: 752px;
  margin: 0 auto;
  padding: 0 8px;
}

.main {
  max-width: 604px;
  margin: 0 auto;
  padding: 0 16px;
}

.btn {
  position: relative;

  display: inline-block;
  padding: 10px 20px;

  color: #fff;
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
  text-decoration: none;

  border-radius: 4px;
  background-color: #e95038;

  transition: all .2s;

  &:hover {
    background-color: darken($color: #e95038, $amount: 10);
  }
}

.header {
  position: sticky;
  top: 0;
  z-index: 999;

  background-color: rgba($color: #fff, $alpha: .25);
  backdrop-filter: blur(10px);

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
  }

  &__logo {
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    text-decoration: none;
    letter-spacing: -1px;
  }

  &__nav {
    @media (max-width: 768px) {
      position: absolute;
      right: 8px;
      top: 50px;

      display: none;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      width: 222px;
      padding: 8px 0;

      border: 1px solid #e2e8f0;
      border-radius: 6px;
      box-shadow: rgba($color: #000, $alpha: .05) 0 1px 2px 0;
      background-color: #fff;

      &--active {
        display: flex;
      }
    }
  }

  &__link {
    position: relative;

    margin-left: 20px;
    padding: 0 8px;

    color: #fff;
    text-decoration: none;
    text-underline-offset: 3px;

    @media (max-width: 768px) {
      width: 100%;
      margin-left: 0;
      padding: 6px 13px !important;

      color: #000;
    }

    &--about {
      display: none;

      @media (max-width: 768px) {
        display: block;
      }
    }

    &--active {
      text-decoration: underline;

      @media (max-width: 768px) {
        text-decoration: none;

        background-color: #e2e8f0;
      }
    }

    &--source {
      padding-left: 23px;

      @media (max-width: 768px) {
        &::before {
          display: none;
        }
      }

      &::before {
        content: '';

        position: absolute;
        top: 50%;
        left: 3px;

        width: 15px;
        height: 15px;

        background: url('/assets/icons/github.svg') no-repeat center / cover;

        transform: translateY(-50%);
      }
    }

    &:hover {
      text-decoration: underline;

      @media (max-width: 768px) {
        text-decoration: none;

        background-color: #e2e8f0;
      }
    }
  }
}

.burger {
  display: none;
  width: 38px;
  height: 38px;
  padding: 0 10px;

  border: 1px solid #e2e8f0;
  border-radius: 7px;

  background: none;
  cursor: pointer;

  &--active {
    background-color: #e2e8f0;
  }

  @media (max-width: 768px) {
    display: block;
  }

  &__line {
    position: relative;

    width: 100%;
    height: 2px;

    background-color: #fff;

    &::before {
      content: '';

      position: absolute;
      top: -5px;
      left: 0;

      width: 100%;
      height: 2px;

      background-color: #fff;
    }

    &::after {
      content: '';

      position: absolute;
      bottom: -5px;
      left: 0;

      width: 100%;
      height: 2px;

      background-color: #fff;
    }
  }
}

.hero {
  margin-top: 40px;

  &__img {
    display: block;
    width: 100%;
    max-width: 428px;
    margin: 0 auto;
  }
}

.content {
  margin-top: 15px;
}

.promo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
  padding: 12px;

  border-radius: 10px;
  background-color: rgba($color: #fff, $alpha: .25);

  &__text {
    text-align: center;
  }
}

.info {
  margin-bottom: 40px;

  &__title {
    font-size: 36px;
    font-weight: 700;
  }
}

.about {
  &__block {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;

    &-title {
      position: relative;

      display: inline-block;
      align-self: flex-start;
      margin-bottom: 15px;

      font-size: 20px;
      font-weight: 700;

      &::before {
        content: '';

        position: absolute;
        bottom: -4px;

        width: 100%;
        height: 4px;

        background-color: #fff;
      }
    }

    &-text {
      text-indent: 16px;
      text-align: justify;
    }

    &-list {
      list-style: none;
    }

    &-link {
      position: relative;

      display: inline-block;
      padding: 8px 16px 8px 41px;

      color: #fff;
      text-decoration: none;
      text-underline-offset: 3px;

      transition: all .2s;

      &:hover {
        border-radius: 4px;
        background-color: rgba($color: #e95038, $alpha: .5);
      }

      &--github {
        &::before {
          content: '';

          position: absolute;
          top: 50%;
          left: 16px;

          width: 15px;
          height: 15px;

          background: url('/assets/icons/github.svg') no-repeat center / cover;

          transform: translateY(-50%);
        }
      }
    }

    &-btn {
      align-self: center;
      margin-top: 15px;
      padding-right: 40px;

      &::before {
        content: '';

        position: absolute;
        top: 50%;
        right: 20px;

        width: 8px;
        height: 11px;

        background: url('/assets/icons/chevron-right.svg') no-repeat center / cover;

        transform: translateY(-50%);
      }
    }
  }
}

.works {
  margin-bottom: 30px;

  &__title {
    margin-bottom: 16px;

    font-size: 20px;
  }

  &__items {
    display: grid;
    grid-gap: 25px;
    grid-template-columns: repeat(2, minmax(0px, 1fr));

    @media (max-width: 768px) {
      grid-template-columns: repeat(1, minmax(0px, 1fr));
    }
  }

  &__item {
    margin-bottom: 24px;

    color: #fff;
    text-align: center;
    text-decoration: none;

    &-img {
      width: 100%;
      margin-bottom: 8px;

      border-radius: 10px;
    }

    &-title {
      font-size: 20px;
      font-weight: 400;
    }

    &-text {
      font-size: 14px;
    }
  }
}

.scripts {
  margin-bottom: 20px;

  &__title {
    margin-bottom: 16px;

    font-size: 20px;
  }

  &__items {
    display: grid;
    grid-gap: 25px;
    grid-template-columns: repeat(2, minmax(0px, 1fr));

    @media (max-width: 768px) {
      grid-template-columns: repeat(1, minmax(0px, 1fr));
    }
  }

  &__item {
    margin-bottom: 24px;

    color: #fff;
    text-decoration: none;

    &-title {
      position: relative;

      padding-left: 20px;

      font-size: 20px;
      font-weight: 400;

      &::before {
        content: '';

        position: absolute;
        left: 0;
        top: 50%;

        width: 15px;
        height: 15px;

        background: url('/assets/icons/github.svg') no-repeat center / cover;

        transform: translateY(-50%);
      }
    }

    &-text {
      font-size: 14px;
    }
  }
}

.cv {
  margin-bottom: 20px;

  &__title {
    margin-bottom: 26px;

    text-align: center;
  }

  &__items {
    display: grid;
    grid-gap: 25px;
    grid-template-columns: repeat(2, minmax(0px, 1fr));

    @media (max-width: 576px) {
      grid-template-columns: repeat(1, minmax(0px, 1fr));

      text-align: center;
    }
  }

  &__item {
    color: #fff;
    text-decoration: none;

    &-title {
      margin-bottom: 10px;

      font-size: 20px;
      font-weight: 400;
    }

    &-img {
      width: 100%;

      border-radius: 10px;
    }
  }
}

.footer {
  padding: 30px 0;

  &__copy {
    font-size: 14px;
    text-align: center;

    opacity: .7;
  }
}
